import React from 'react';
import Types from 'prop-types';
import classNames from 'classnames';

export const BUTTON_VARIANTS = ['primary', 'success', 'default', 'danger'];
export const BUTTON_SIZES = ['xs', 'sm', 'md', 'lg'];
// export const BUTTON_LAYOUTS = ['default', 'dropdown', 'split']; TODO: needs more discussion regarding
export const BUTTON_STATES = [
  'default',
  'loading',
  'disabled',
  // 'failed', TODO: needs animated loading indicator with failed state
  // 'succeeded' TODO: needs animated loading indicator with succeeded state
  // See more in the styleguide #Submit section: http://transferwise.github.io/styleguide-components/index.html#submit
];

const Button = ({ label, buttonState, size, variant, isBlock, onClick }) => {
  const isDisabled = buttonState === 'disabled';
  const isLoading = buttonState === 'loading';

  const classes = classNames(`btn btn-${size}`, {
    [`btn-loading`]: isLoading,
    [`btn-${variant}`]: variant,
    [`btn-block`]: isBlock,
  });

  return (
    <button
      type="button"
      className={classes}
      onClick={isDisabled ? null : e => onClick(e)}
      disabled={isDisabled || buttonState === 'loading'}
    >
      {label}
      {isLoading && <span className={`btn-loader ${isBlock ? 'pull-left' : 'm-l-2'}`} />}
    </button>
  );
};

Button.propTypes = {
  variant: Types.oneOf(BUTTON_VARIANTS),
  size: Types.oneOf(BUTTON_SIZES),
  buttonState: Types.oneOf(BUTTON_STATES),
  isBlock: Types.bool,
  onClick: Types.func.isRequired,
  label: Types.string.isRequired,
};

Button.defaultProps = {
  size: 'md',
  variant: 'primary',
  buttonState: 'default',
  isBlock: false,
};

export default Button;



