import React, { Component } from 'react';
import { Button, Select, Checkbox } from '../src';

const SIZES = ['xs', 'sm', 'md', 'lg'];
const VARIANTS = ['primary', 'success', 'default', 'danger'];
const STATES = ['default', 'loading', 'disabled'];

export default class ButtonDocs extends Component {
  constructor(props) {
    super(props);
    this.state = {
      label: 'Send money',
      stateSelection: { value: 'default', label: 'default' },
      variant: 'success',
      size: 'md',
      isBlock: false,
      onClick: () => null,
    };
  }

  createStateLink(name) {
    return value => this.setState({ [name]: value });
  }

  render() {
    const { label, stateSelection, size, variant, isBlock, onClick } = this.state;
    const buttonLabel = label === '' ? 'Send Money' : label;

    return (
      <div className="container">
        <section className="section">
          <div className="row">
            <div className="col-md-6">
              <h2>Button</h2>
              <p>Simple wrapper component for all button variants and their states</p>
            </div>
            <div className="col-md-6">
              <Button
                label={buttonLabel}
                size={size}
                buttonState={stateSelection.value}
                variant={variant}
                isBlock={isBlock}
                onClick={onClick}
              />
            </div>
          </div>
          <div className="row">
            <div className="col-md-6">
              <pre className="tw-docs-code">
                {`<Button
  label="${buttonLabel}"
  size="${size}"
  variant="${variant}"
  state="${stateSelection.value}"
  isBlock="${isBlock}" 
  onClick="this.handleClick"
/>`}
              </pre>
            </div>
            <div className="col-md-6">
              <div className="row">
                <div className="col-md-12">
                  <label htmlFor="button-label-input" className="control-label">
                    Label
                  </label>
                  <input
                    id="button-label-input"
                    type="text"
                    value={label}
                    onChange={event => this.setState({ label: event.target.value })}
                    placeholder="Add button label.."
                    className="form-control"
                  />
                </div>
              </div>
              <div className="m-t-3" />
              <div className="row">
                <div className="col-md-6">
                  <label htmlFor="button-state-selector" className="control-label">
                    State
                  </label>
                  <Select
                    id="button-state-selector"
                    required
                    selected={stateSelection}
                    options={STATES.map(state => ({
                      value: state,
                      label: !state ? 'default' : state,
                    }))}
                    onChange={selection => this.setState({ stateSelection: selection })}
                  />
                </div>
                <div className="col-md-6">
                  <label htmlFor="button-block-checkbox" className="control-label">
                    Block (Full size of container)
                  </label>
                  <Checkbox
                    label="isBlock"
                    checked={isBlock}
                    onChange={this.createStateLink('isBlock')}
                  />
                </div>
              </div>
              <div className="m-t-3" />
              <div className="row">
                <div className="col-md-6">
                  <label htmlFor="button-size-selector" className="control-label">
                    Size
                  </label>
                  <Select
                    id="button-size-selector"
                    required
                    selected={{ value: size, label: size }}
                    options={SIZES.map(sizeValue => ({ value: sizeValue, label: sizeValue }))}
                    onChange={selection => this.setState({ size: selection.value })}
                  />
                </div>
                <div className="col-md-6">
                  <label htmlFor="button-variant-selector" className="control-label">
                    Variant
                  </label>
                  <Select
                    id="button-variant-selector"
                    required
                    selected={{ value: variant, label: variant }}
                    options={VARIANTS.map(variantKey => ({ value: variantKey, label: variantKey }))}
                    onChange={selection => this.setState({ variant: selection.value })}
                  />
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}



